<template>
  <div class="mdl-grid order">
    <div class="mdl-cell--12-col-desktop	mdl-cell--4-col-phone"  v-show="clientSelectView">
      <div class="text-center">
        <client-view
          :enableManual="false"
          :data="clients.data"
          :action="fetchClients"
          :fk="'client'"
          @clientSelected="clientParams=$event"
          ref="client"
          :status="''"
          :dependOnStatus="false"
          :displayDetails="false"
          :placeholder="'Select Client'"
        >
        </client-view>
      </div>
    </div>
     <div class="mdl-cell--6-col-desktop	mdl-cell--4-col-phone" v-show="!clientSelectView">
      <div>
        <ul class="mdl-list">
          <li class="mdl-list__item mdl-list__item--two-line">
            <span class="mdl-list__item-primary-content">
              <i class="material-icons mdl-list__item-avatar">person</i>
              <strong>{{clientParams.name}}</strong>
              <span class="mdl-list__item-sub-title">
                {{clientParams.email}}
              </span>
              <span class="mdl-list__item-sub-title">
                {{clientParams.address}}
              </span>
              <span class="mdl-list__item-sub-title">
                <strong>{{clientParams.contact_number}}</strong>
              </span>
            </span>
          </li>
        </ul>
      </div>
    </div>
    <div class="mdl-cell--6-col-desktop	mdl-cell--4-col-phone" v-show="!clientSelectView">
      <ul class="mdl-list">
        <li class="mdl-list__item mdl-list__item--two-line">
          <span class="mdl-list__item-primary-content">
            <strong>GST Number</strong>
            <span class="mdl-list__item-sub-title">
              {{clientParams.gst_number}}
              </span>
          </span>
          <span class="mdl-list__item-secondary-content"  @click="toggleClientView">
            <a class="mdl-list__item-secondary-action" href="#"><i class="material-icons">create</i></a>
          </span>
        </li>
        <li class="mdl-list__item mdl-list__item--two-line">
          <span class="mdl-list__item-primary-content">
            <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('procurementNumber')}">
              <input v-validate="'required'" name="procurementNumber" id="procurementNumber" class="mdl-textfield__input" v-model="params.procurement_number">
              <label class="mdl-textfield__label" for="name">Procurement Number</label>
              <span class="mdl-textfield__error">{{ errors.first('procurementNumber') }}</span>
            </div>
          </span>
        </li>
      </ul>
    </div>
    <div class="mdl-cell--12-col-desktop	mdl-cell--4-col-phone  text-center" v-show="clientParams.name">
      <hr>
      <span class="">Products</span>
      <hr>
      <line-items-view
        :data="products.data"
        :action="fetchProducts"
        :field="'name'"
        :fk="'product'"
        ref="products"
        @selectedLineItems="lineItemParams=$event"
        @totalSelectedItemsAmount="total=$event"
      >
      </line-items-view>
    </div>
    <div class="mdl-cell--12-col-desktop	mdl-cell--4-col-phone text-right" v-show="!clientSelectView">
      <hr>
      <strong>Total   {{total}}</strong>
      <hr>
    </div>
  </div>
</template>
<script>
import { mapActions, mapGetters } from 'vuex'
import { ClientView, LineItemsView } from 'SHARED/orders/components'
export default {
  name: 'procurement-form',
  props: {
    afterSuccess: {
      type: Function
    }
  },
  components: {
    ClientView,
    LineItemsView
  },
  computed: {
    ...mapGetters({
      clients: 'getClients',
      products: 'getProducts'
    }),
    clientParams: {
      get () {
        return this.client
      },
      set (data) {
        this.params.client = data.client
        this.client = data
        this.clientSelectView = false
      }
    },
    lineItemParams: {
      get () {
        return this.params.line_items
      },
      set (data) {
        this.$set(this.params, 'line_items', data)
      }
    }
  },
  methods: {
    ...mapActions([
      'fetchClients',
      'fetchProducts',
      'addProcurement'
    ]),
    toggleClientView () {
      this.clientSelectView = !this.clientSelectView
    },
    proceed () {
      this.$validator.validateAll().then(valid => {
        if (valid) {
          return this.$refs.client.valid()
        }
      }).then(valid => {
        if (valid) {
          return this.$refs.products.valid()
        }
      }).then(valid => {
        if (valid) {
          this.addProcurement(this.params).then(() => {
            this.afterSuccess(false)
            this.$toastr.s('success')
          }).catch(e => {
            this.errors.push(e.response.data.errors)
            this.$toastr.e(e)
          })
        }
      })
    }
  },
  data () {
    return {
      clientSelectView: true,
      params: {
        client: '',
        line_items: [],
        procurement_number: ''
      },
      client: {},
      total: 0
    }
  }
}
</script>



